<template>
  <div>
    <div class="container">
      <div class="cart">
        <table>
          <thead>
            <tr>
              <th width="120"></th>
              <th width="400">商品信息</th>
              <th width="220">单价</th>
              <th width="180">数量</th>
              <th width="180">小计</th>
              <th width="140">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in cartList" :key="item.skuId">
              <td>
                <input
                  type="checkbox"
                  v-model="item.checked"
                  @change="handleChange"
                />
              </td>
              <td>
                <div class="goods">
                  <router-link to="/"
                    ><img :src="item.picture" alt=""
                  /></router-link>
                  <div>
                    <p class="name ellipsis">
                      {{ item.name }}
                    </p>
                    <p class="attr">{{ item.attrsText }}</p>
                  </div>
                </div>
              </td>
              <td class="tc">
                <p>&yen;{{ item.nowPrice }}</p>
                <p>
                  比加入时降价
                  <span class="price"
                    >&yen;{{ (item.price - item.nowPrice) | priceFormat }}</span
                  >
                </p>
              </td>
              <td class="tc">{{ item.count }}</td>
              <td class="tc">
                <p class="f16 price">
                  &yen;{{ (item.nowPrice * item.count) | priceFormat }}
                </p>
              </td>
              <td class="tc">
                <p>
                  <span class="delete" @click="openMsg(item.skuId)">删除</span>
                </p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="action">
        <div class="batch">
          <!-- <input type="checkbox" v-model="allcheck" /> -->
          <!-- <span>全选</span> -->
        </div>
        <div class="total">
          共 {{ total }} 件商品，已选择 {{ checkNum }} 件，合计：
          <span class="price">{{ checkPrice | priceFormat }}</span>
          <el-button type="primary" @click="handleClick">结算</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cartList: [],
      total: 0,
      checkList: [],
      checkNum: 0,
      checkPrice: 0,
      deleteIds: [],
    };
  },
  filters: {
    priceFormat(val) {
      return val.toFixed(2);
    },
  },
  methods: {
    async getCartList() {
      const { data } = await this.$axios.get("/member/cart");
      // console.log(data);
      this.cartList = data.result;
      this.cartList.forEach((item) => {
        item.checked = false;
      });
      this.getTotal();
      this.getCheckInfo();
    },
    openMsg(id) {
      this.$confirm("您是否确认删除该商品?", "温馨提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.deleteIds.push(id);
        // console.log(this.deleteIds);
        this.deleteCart();
      });
    },
    async deleteCart() {
      const { data } = await this.$axios.delete("/member/cart", {
        data: {
          ids: this.deleteIds,
        },
      });
      // console.log(data);
      this.deleteIds = [];
      this.getCartList();
    },
    getTotal() {
      this.total = 0;
      this.cartList.forEach((item) => {
        this.total = this.total + item.count;
      });
    },
    getCheckInfo() {
      this.checkList = [];
      this.checkNum = 0;
      this.checkPrice = 0;
      this.cartList.forEach((item) => {
        if (item.checked === true) {
          this.checkList.push(item);
          this.checkNum = this.checkNum + item.count;
          this.checkPrice = this.checkPrice + item.nowPrice * item.count;
        }
      });
    },
    handleChange() {
      this.getCheckInfo();
    },
    handleClick() {
      if (this.checkList.length === 0) {
        this.$message.error("请至少选择一样商品");
      } else {
        this.$store.commit("setCheckInfo", {
          checkList: this.checkList,
          checkNum: this.checkNum,
          checkPrice: this.checkPrice,
        });
        this.$router.push("/user/check");
      }
    },
  },
  created() {
    this.getCartList();
  },
};
</script>

<style  lang="less" scoped>
div {
  .cart {
    margin-top: 30px;
    background: #ffffff;
    color: #606266;
    table {
      border-spacing: 0;
      border-collapse: collapse;
      line-height: 24px;
      th {
        font-size: 16px;
        font-weight: normal;
        line-height: 50px;
        padding: 10px;
        border-bottom: 1px solid #dcdfe6;
        &:first-child {
          text-align: left;
          padding-left: 30px;
          color: #909399;
        }
      }
      td {
        padding: 10px;
        border-bottom: 1px solid #dcdfe6;
        &:first-child {
          text-align: left;
          padding-left: 30px;
          color: #909399;
        }
        input {
          width: 20px;
          height: 20px;
        }
        .goods {
          display: flex;
          align-items: center;
          img {
            width: 100px;
            height: 100px;
          }
          > div {
            width: 280px;
            font-size: 16px;
            padding-left: 10px;
            .attr {
              font-size: 14px;
              color: #909399;
            }
          }
        }
      }
      .tc {
        text-align: center;
        .price {
          color: #cf4444;
        }
        .f16 {
          font-size: 16px;
        }
        .delete {
          color: #409eff;
          cursor: pointer;
        }
      }
    }
  }
  .action {
    display: flex;
    background: #ffffff;
    margin-top: 30px;
    height: 80px;
    align-items: center;
    font-size: 16px;
    justify-content: space-between;
    padding: 0 30px;
    .batch {
      display: flex;
      align-items: center;
      input {
        width: 16px;
        height: 16px;
      }
      span {
        margin-left: 10px;
      }
    }
    .total {
      .price {
        font-size: 18px;
        margin-right: 20px;
        font-weight: bold;
        color: #cf4444;
      }
      .el-button {
        width: 120px;
        height: 50px;
        font-size: 16px;
      }
    }
  }
}
</style>