<template>
  <div class="section">
    <div class="wrapper">
      <nav>账户登录</nav>
      <div class="form">
        <div class="form-item">
          <el-input prefix-icon="el-icon-user" v-model="loginInfo.account">
          </el-input>
        </div>
        <div class="form-item">
          <el-input
            type="password"
            prefix-icon="el-icon-lock"
            v-model="loginInfo.password"
          >
          </el-input>
        </div>
        <div class="form-item">
          <div class="agree">
            <input type="checkbox" checked />
            <span>我已同意</span>
            <a href="javascript:;">《隐私条款》</a>
            <span>和</span>
            <a href="javascript:;">《服务条款》</a>
          </div>
        </div>
        <el-button type="primary" @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginInfo: {
        account: "xiaotuxian002",
        password: "123456",
      },
    };
  },
  methods: {
    async login() {
      const { data } = await this.$axios.post("/login", this.loginInfo);
      this.$store.commit("setUserInfo", data.result);
      this.$message.success("登录成功");
      this.$router.push("/");
    },
  },
};
</script>

<style lang="less" scoped>
.section {
  height: 488px;
  position: relative;
  .wrapper {
    width: 380px;
    background: #ffffff;
    position: absolute;
    left: 50%;
    top: 50px;
    transform: translate3d(100px, 0, 0);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    nav {
      height: 55px;
      border-bottom: 1px solid #dcdfe6;
      font-size: 20px;
      font-weight: 700;
      color: #409eff;
      line-height: 55px;
      text-align: center;
    }
    .form {
      padding: 40px 40px;
      &-item {
        margin-bottom: 28px;
        .agree {
          text-align: right;
          a {
            color: #069;
          }
        }
      }
      .el-button {
        width: 100%;
        height: 40px;
      }
    }
  }
}
</style>