import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  userInfo: {},
  checkInfo: {}
}
const mutations = {
  setUserInfo(state, data) {
    state.userInfo = data
  },
  setCheckInfo(state, data) {
    state.checkInfo = data
  }
}
const actions = {}


const store = new Vuex.Store({
  state,
  mutations,
  actions
})

export default store
