<template>
  <div>
    <CateFilter
      :id="id"
      :cateList="cateList"
      :activeFilter="activeFilter"
      @filterChange="getFilter"
    ></CateFilter>
    <div class="show">
      <div class="container">
        <div class="rank">
          <button
            type="button"
            :class="{ active: activeRank === null }"
            @click="toggleRank(null, '')"
          >
            默认排序
          </button>
          <button
            type="button"
            :class="{ active: activeRank === 0 }"
            @click="toggleRank(0, 'publishTime')"
          >
            最新商品
          </button>
          <button
            type="button"
            :class="{ active: activeRank === 1 }"
            @click="toggleRank(1, 'orderNum')"
          >
            销量最高
          </button>
          <button
            type="button"
            :class="{ active: activeRank === 2 }"
            @click="toggleRank(2, 'evaluateNum')"
          >
            评论最多
          </button>
          <button
            type="button"
            :class="{ active: activeRank === 3 }"
            @click="toggleRank(3, 'price')"
          >
            价格<i
              v-if="
                queryInfo.sortField === 'price' &&
                queryInfo.sortMethod === 'desc'
              "
              class="el-icon-bottom"
            ></i
            ><i
              v-if="
                queryInfo.sortField === 'price' &&
                queryInfo.sortMethod === 'asc'
              "
              class="el-icon-top"
            ></i>
          </button>
        </div>
        <CateGoods :goodsList="goodsList"></CateGoods>
      </div>
    </div>
  </div>
</template>

<script>
import CateFilter from "./components/CateFilter.vue";
import CateGoods from "./components/CateGoods.vue";
export default {
  components: {
    CateFilter,
    CateGoods,
  },
  props: ["id"],
  data() {
    return {
      activeFilter: null,
      activeRank: null,
      goodsList: [],
      cateList: [],
      queryInfo: {
        categoryId: this.id,
        sortField: "",
        sortMethod: "desc",
        pageSize: 100,
      },
    };
  },
  methods: {
    async getCateList() {
      const { data } = await this.$axios.get("/category", {
        params: {
          id: this.id,
        },
      });
      // console.log(data);
      this.cateList = data.result.children;
    },
    async getGoodsList() {
      const { data } = await this.$axios.post("/category/goods", this.queryInfo);
      // console.log(data);
      this.goodsList = data.result.items;
    },
    getFilter(data) {
      // console.log(data);
      this.activeFilter = data.index;
      this.queryInfo.categoryId = data.id;
    },
    toggleRank(index, sort) {
      this.activeRank = index;
      if (sort === "price") {
        this.queryInfo.sortMethod =
          this.queryInfo.sortMethod === "desc" ? "asc" : "desc";
      } else {
        this.queryInfo.sortMethod = "desc";
      }
      this.queryInfo.sortField = sort;
    },
  },
  created() {
    this.getGoodsList();
    this.getCateList();
  },
  watch: {
    id: {
      handler() {
        this.activeFilter = null;
        this.activeRank = null;
        this.queryInfo.categoryId = this.id;
        this.queryInfo.sortField = "";
        this.queryInfo.sortMethod = "desc";
      },
    },
    queryInfo: {
      deep: true,
      handler() {
        this.getGoodsList();
        this.getCateList();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.show {
  .rank {
    padding: 30px 50px;
    display: flex;
    background-color: #ffffff;
    button {
      border: 1px solid #dcdfe6;
      background-color: #ffffff;
      outline: none;
      padding: 8px 20px;
      margin-right: 20px;
      color: #606266;
      border-radius: 2px;
      &:hover {
        cursor: pointer;
      }
      &.active {
        background: #409eff;
        border-color: #409eff;
        color: #ffffff;
      }
      i {
        margin-left: 5px;
      }
    }
  }
}
</style>