<template>
  <div class="filter">
    <div class="container">
      <div class="head">分类</div>
      <div class="body">
        <span
          :class="{ active: activeFilter === null }"
          @click="toggleFilter(null, id)"
          >全部</span
        >
        <span
          v-for="(item, index) in cateList"
          :key="index"
          @click="toggleFilter(index, item.id)"
          :class="{ active: activeFilter === index }"
          >{{ item.name }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["id", "cateList", "activeFilter"],
  methods: {
    toggleFilter(index, id) {
      // console.log(index,id);
      this.$emit("filterChange", { index, id });
    },
  },
};
</script>

<style lang="less" scoped>
.filter {
  margin: 30px 0;
  .container {
    background: #ffffff;
    padding: 25px;
    display: flex;
    line-height: 40px;
    .head {
      margin-right: 40px;
      color: #606266;
    }
    .body {
      flex: 1;
      span {
        margin-right: 35px;
        &.active {
          color: #409eff;
        }
        &:hover {
          cursor: pointer;
          color: #409eff;
        }
      }
    }
  }
}
</style>