import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import Layout from '@/views/layout/Layout.vue'
import Home from '@/views/home/Home.vue'
import Login from '@/views/login/Login.vue'
import Category from '@/views/category/Category.vue'
import Goods from '@/views/goods/Goods.vue'
import User from '@/views/user/User.vue'
import Account from '@/views/account/Account.vue'
import Order from '@/views/order/Order.vue'
import Cart from '@/views/cart/Cart.vue'
import Check from '@/views/check/Check.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Layout,
    children: [
      { path: '/', component: Home },
      { path: 'category/:id', component: Category, props: true },
      { path: 'goods/:id', component: Goods, props: true },
      {
        path: 'user',
        component: User,
        children: [
          { path: '/', redirect: 'account' },
          { path: 'account', component: Account },
          { path: 'order', component: Order }
        ]
      },
      { path: 'user/cart', component: Cart },
      { path: 'user/check', component: Check },
    ]
  },
  { path: '/login', component: Login },
]

const router = new VueRouter({
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 }
  }
})

router.beforeEach((to, form, next) => {
  if (to.path.startsWith('/user') && !store.state.userInfo.token) {
    next('/login')
  } else {
    next()
  }
})

export default router
