<template>
  <div>
    <div class="container">
      <h3 class="title">收货地址</h3>
      <div class="body">
        <div class="address">
          <div class="text">
            <ul>
              <li><span>收货人：</span>收货人</li>
              <li><span>联系方式：</span>联系方式</li>
              <li><span>收货地址：</span>收货地址</li>
            </ul>
          </div>
        </div>
      </div>
      <h3 class="title">商品信息</h3>
      <div class="body">
        <table class="goods">
          <thead>
            <tr>
              <th width="520">商品信息</th>
              <th width="170">单价</th>
              <th width="170">数量</th>
              <th width="170">小计</th>
              <th width="170">实付</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="item in $store.state.checkInfo.checkList"
              :key="item.skuId"
            >
              <td>
                <div class="info">
                  <img :src="item.picture" alt="" />
                  <div class="desc">
                    <p>{{ item.name }}</p>
                    <p>{{ item.attrsText }}</p>
                  </div>
                </div>
              </td>
              <td>&yen;{{ item.nowPrice }}</td>
              <td>{{ item.count }}</td>
              <td>&yen;{{ item.nowPrice * item.count }}</td>
              <td class="price">&yen;{{ item.nowPrice * item.count }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <h3 class="title">配送时间</h3>
      <div class="body">
        <span class="btn" :class="{ active: activeDate === 0 }"
          >周一至周日</span
        >
        <span class="btn" :class="{ active: activeDate === 1 }"
          >周一至周五</span
        >
        <span class="btn" :class="{ active: activeDate === 2 }"
          >周末及节假日</span
        >
      </div>
      <h3 class="title">支付方式</h3>
      <div class="body">
        <span class="btn" :class="{ active: activePay === 0 }">在线支付</span>
        <span class="btn" :class="{ active: activePay === 1 }">货到付款</span>
      </div>
      <h3 class="title">金额明细</h3>
      <div class="body">
        <div class="total">
          <dl>
            <dt>商品件数：</dt>
            <dd>{{ $store.state.checkInfo.checkNum }}件</dd>
          </dl>
          <dl>
            <dt>商品总价：</dt>
            <dd>&yen;{{ $store.state.checkInfo.checkPrice }}</dd>
          </dl>
          <dl>
            <dt>运<i></i>费：</dt>
            <dd>&yen;0.00</dd>
          </dl>
          <dl>
            <dt>优<i></i>惠：</dt>
            <dd>&yen;0.00</dd>
          </dl>
          <dl>
            <dt>应付金额：</dt>
            <dd class="price">&yen;{{ $store.state.checkInfo.checkPrice }}</dd>
          </dl>
          <el-button type="primary">提交订单</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeDate: 0,
      activePay: 0,
    };
  },
};
</script>

<style scoped lang="less">
div {
  .container {
    margin-top: 30px;
    background: #fff;
    padding: 0 20px;
    .title {
      font-size: 16px;
      font-weight: normal;
      padding-left: 10px;
      line-height: 70px;
      border-bottom: 1px solid #dcdfe6;
    }
    .body {
      padding: 20px 0;
      .address {
        border: 1px solid #dcdfe6;
        display: flex;
        align-items: center;
        .text {
          flex: 1;
          min-height: 90px;
          display: flex;
          align-items: center;
          ul {
            flex: 1;
            padding: 20px;
            li {
              line-height: 30px;
              span {
                color: #909399;
                margin-right: 5px;
              }
            }
          }
        }
      }
      .goods {
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        tr {
          th {
            background: #dcdfe6;
            font-weight: normal;
          }
          td,
          th {
            text-align: center;
            padding: 20px;
            border-bottom: 1px solid #dcdfe6;
            &:first-child {
              border-left: 1px solid #dcdfe6;
            }
            &:last-child {
              border-right: 1px solid #dcdfe6;
            }
            &.price {
              color: #cf4444;
            }
            .info {
              display: flex;
              text-align: left;
              img {
                width: 70px;
                height: 70px;
                margin-right: 20px;
              }
              .desc {
                line-height: 24px;
                p {
                  &:last-child {
                    color: #909399;
                  }
                }
              }
            }
          }
        }
      }
      .btn {
        width: 228px;
        height: 50px;
        border: 1px solid #dcdfe6;
        text-align: center;
        line-height: 48px;
        margin-right: 25px;
        color: #606266;
        display: inline-block;
        cursor: pointer;
        &.active,
        &:hover {
          border-color: #409eff;
        }
      }
      .total {
        dl {
          display: flex;
          justify-content: flex-start;
          line-height: 50px;
          margin-left: 30px;
          dt {
            i {
              display: inline-block;
              width: 2em;
            }
          }
          dd {
            width: 240px;
            text-align: right;
            padding-right: 70px;
            &.price {
              font-size: 20px;
              color: #cf4444;
            }
          }
        }
        .el-button {
          margin: 30px;
          width: 180px;
          height: 50px;
          font-size: 16px;
        }
      }
    }
  }
}
</style>